*{
    margin: 0px 0px;
}
.header_list
{
    margin: 10px;
    padding: 3px;
    border: 1px solid rgb(182, 175, 175);
    background-image: linear-gradient(to right, rgba(255,0,0,0), rgb(238, 135, 52));
    display: flex;
    justify-content: space-between;
    
}
.imgstart{
    width: 100px;
    border-radius: 40px;
}
.nav_list > ul{
    display: flex;
    justify-content: left;
    margin: 10px;
    padding: 5px;
}
.nav_list >ul >li{
    list-style: none;
    padding: 10px;
    font-size: 30px;
}
/*body*/
.rest-list{
    margin: 1px;
    padding: 5px;
    display: flex;
    flex-wrap: wrap;
}
.rest-list>.card{
    width: 200px;
    padding: 5px;
    margin: 10px;
    border: 1px solid black;
    border-radius: 10px;
    box-shadow: 8px 10px rgb(171, 198, 222);

}
img{
    width: 100%;
}
.restlist{
    display: flex;
    flex-wrap: wrap;
}